@import '../../styles/colors';
@import '../../styles/fonts';

.shrine {
  line-height: 48px;
  font-size: 24px;
  .counter {
    text-align: right;
    padding: 0 16px 0 0;
  }
  .diff {
    @include f-reg;
    &.is-ahead {
      color: $warm-green;
    }
    &.is-pb {
      @include gold;
    }
  }
  .time {
    @include f-reg;
    font-size: 24px;
    text-align: right;
    color: $pale-blue;
    .ms {
      font-size: 75%;
    }
  }
}

.shrine.infolog {
  .counter {
    margin: 0 0 0 auto;
  }
}

.shrine.current {
  margin: 24px auto;
  .counter {
    grid-row: 1/3;
  }
  .name {
    @include f-maj;
    line-height: 48px;
  }
  .desc {
    @include f-sec;
    font-size: 20px;
    line-height: 32px;
    grid-row: 2;
    grid-column: 2 / 3;
  }
  .time {
    @include f-majsec;
    color: $pale-blue;
    grid-column: 3/5;
    grid-row: 1/3;
    &.is-paused {
      color: $cloudy-gray;
    }
    &.is-initial {
      color: $milky-white;
    }
    .delta {
      @include f-reg;
      margin: 12px 0 -12px;
    }
    .ms {
      font-size: 66%;
    }
  }
}

@media (max-width: 480px) {
  .shrine.current {
    grid-template-rows: 32px 24px;
    .counter {
      grid-row: 1;
    }
    .name {
      grid-column: 2/5;
      grid-row: 1;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .desc {
      @include fz-15;
      grid-column: 2/3;
      grid-row: 2;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .time {
      grid-row: 2;
    }
  }
}
